import { Row, Col, Container } from 'react-bootstrap';
import sideSVG from '../assets/images/lappi.svg';
import '../styles/About.css';

export const About = () => {
    return (
        <div id="about" className='components'>
            <Container fluid className="py-5">
                <h1>About Me</h1>
                <Row>
                    <Col sm={6}>
                        <img className="side-img" src={sideSVG}></img>
                    </Col>
                    <Col sm={6} className="py-sm-5">
                        <Row className="py-sm-4">
                            <p>
                                Building cutting-edge, user-friendly websites and apps is a true passion of mine.
                                I actively lookout for new technologies and stay up-to-date on industry trends.
                                {/* Looking for SDE roles related to Frontend, Backend and Full Stack Development.
                                I am also open to freelance and contractual opportunities */}
                            </p>
                            <p>
                                Technologies I have worked with:
                            </p>
                        </Row>
                        <Row>
                            <Col >
                                <ul>
                                    <li>
                                        Angular
                                    </li>
                                    <li>
                                        React
                                    </li>
                                    <li>
                                        Express
                                    </li>
                                    <li>
                                        Node
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul>
                                    <li>
                                        MS SQL
                                    </li>
                                    <li>
                                        MongoDB
                                    </li>
                                    <li>
                                        Spring Boot
                                    </li>
                                    <li>
                                        Azure
                                    </li>
                                </ul>
                            </Col>

                        </Row>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}