import '../styles/Social.css';
import {social} from '../assets/data/links';
import linkedin from '../assets/logos/linkedin.svg'
import github from '../assets/logos/github-logo.svg'

export const Social = () => {
    return (
        <div className="social">
            <a href={social.linkedin} target="_blank"><img src={linkedin} alt="linkedin"></img></a>
            <a href={social.github} target="_blank"><img src={github} alt="github"></img></a>
        </div>
    );
}