import { Container, Row, Col } from "react-bootstrap";
import logo from '../assets/images/banner.svg';
import '../styles/Banner.css';
import { Social } from "./Social";

export const Banner = () => {
    return (
        <div id="banner" className="banner">
            <Container fluid className="">
                <Row>
                    <Col sm={6}>
                        <p>Hello there! My name is</p>
                        <h1> Manoj Hosmath </h1>
                        <h3 className="banner-h3"> I'm a Full Stack Web Developer </h3>
                        <br />
                        <p> I am passionate about designing and developing
                            elegant web solutions.
                        </p>
                        <br />
                        <Social></Social>
                        {/* <a href="#">
                            <img src={logo} width="100" height="50"> </img>
                        </a> */}
                    </Col>
                    <Col sm={6}>
                        {/* <Col className="d-none d-md-block d-lg-block"> */}
                        <div className="d-flex justify-content-center">
                            <img className="flying" src={logo}></img>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}