import { Container } from "react-bootstrap";
import resumeData from "../assets/data/resumeData";
import VerticalTab from "../libs/vertical-tab-table/VerticalTab";

export const Experience = () => {
    return (
        <div id="experience" className='components'>
            <Container fluid className="py-5">
                <h1>Experience</h1>
                <VerticalTab data={resumeData.jobs} />
            </Container>
        </div>
    );
}