import { Button, Card, Container } from "react-bootstrap";
import '../styles/Contact.css'

export const Contact = () => {
    return (
        <div id="contact" className="contact-div components">
            <Container fluid className="py-5 d-flex justify-content-center">
                <Card className="contact-card bg-dark text-white text-center">
                    <Card.Body>
                        <Card.Title><h1>Contact</h1></Card.Title>
                        <Card.Text>
                            Feel free to ping me to discuss ideas, inviting for events, hire me for work,
                            or even for partnering up to build a startup {':)'}
                        </Card.Text>
                        <Button href="mailto:6u0iwvwgz@mozmail.com" variant="outline-light"> Drop a mail </Button>
                    </Card.Body>
                </Card >
            </Container>
        </div>
    );
}