import { Container, Card, Button, Row, Col } from "react-bootstrap";
import projectsData from "../assets/data/projectsData.js";
import '../styles/Projects.css';
import github from '../assets/logos/github-logo.svg'

export const Projects = () => {
    return (
        <div id="projects" className='components'>
            <Container fluid className="py-5">
                <h1> Projects </h1>
                <Row>

                    {projectsData.projects.map((data, index) => {
                        return (
                            <Col className="py-4 col-sm-4" key={index}>
                                <Card className="projects-card bg-dark text-white text-center">
                                    <Card.Body>
                                        <Card.Title><h3>{data.project.name}</h3></Card.Title>
                                        <Card.Text>
                                            {data.project.description}
                                        </Card.Text>
                                        <Button href={data.project.github} target="_blank" variant="outline-light"> Github </Button>
                                    </Card.Body>
                                </Card >
                            </Col>
                        );
                    })}

                </Row>
            </Container>
        </div>
    );
}